<style lang="less">
  @import '../../styles/common.less';
  @import '/components/table.less';
</style>
<template>
  <div>
    <Row>
      <Card>
        <p slot="title">
          <Icon type="md-person"></Icon>
          我的工单
        </p>
        <Row>
          <Col span="24">
            <Table border :columns="columns" :data="table_data" stripe size="small"></Table>
          </Col>
        </Row>
        <br>
        <Page :total="page_number" show-elevator @on-change="currentpage" :page-size="20"></Page>
      </Card>
    </Row>
  </div>
</template>
<script>
  //
  import axios from 'axios'
  import util from '../../libs/util'

  export default {
    name: 'put',
    data () {
      return {
        columns: [
          {
            title: '工单编号:',
            key: 'work_id',
            sortable: true
          },
          {
            title: '工单说明',
            key: 'text'
          },
          {
            title: '是否备份',
            key: 'backup',
            width: 90
          },
          {
            title: '提交时间:',
            key: 'date',
            sortable: true
          },
          {
            title: '提交人',
            key: 'username',
            width: 120,
            sortable: true
          },
          {
            title: '审核人',
            key: 'auditor',
            width: 120
          },
          {
            title: 'DBA',
            key: 'executor',
            width: 120
          },
          {
            title: '执行时间',
            key: 'exe_date'
          },
          {
            title: '状态',
            key: 'status',
            render: (h, params) => {
              const row = params.row
              let color = ''
              let text = ''
              if (row.status === 2) {
                color = 'primary'
                text = '待Leader审核'
              } else if (row.status === 0) {
                color = 'error'
                text = '驳回'
              } else if (row.status === 1) {
                color = 'success'
                text = '已执行'
              } else if (row.status === 4) {
                color = 'error'
                text = '执行或备份失败'
              } else if (row.status === 5) {
                color = 'warning'
                text = '执行成功有警告'
              } else if (row.status === 6) {
                color = 'primary'
                text = '待DBA执行'
              } else {
                color = 'warning'
                text = '执行中'
              }

              return h('Tag', {
                props: {
                  type: 'dot',
                  color: color
                }
              }, text)
            },
            sortable: true,
            filters: [{
              label: '已执行',
              value: 1
            },
              {
                label: '驳回',
                value: 0
              },
              {
                label: '待Leader审核',
                value: 2
              },
              {
                label: '执行中',
                value: 3
              },
              {
                label: '执行失败',
                value: 4
              },
              {
                label: '执行成功有警告',
                value: 5
              },
              {
                label: '待DBA执行',
                value: 6
              }
            ],
            //            filterMultiple: false 禁止多选,
            filterMethod (value, row) {
              if (value === 1) {
                return row.status === 1
              } else if (value === 2) {
                return row.status === 2
              } else if (value === 0) {
                return row.status === 0
              } else if (value === 3) {
                return row.status === 3
              } else if (value === 6) {
                return row.status === 6
              } else if (value === 5) {
                return row.status === 5
              } else {
                return row.status === 4
              }
            }
          },
          {
            title: '操作',
            key: 'action',
            align: 'center',
            render: (h, params) => {
              if (params.row.status === 0) {
                return h('div', [
                  h('Button', {
                    props: {
                      size: 'small',
                      type: 'text'
                    },
                    on: {
                      click: () => {
                        this.$router.push({
                          name: 'orderlist',
                          query: {
                            workid: params.row.work_id,
                            id: params.row.id,
                            status: params.row.status,
                            type: params.row.type
                          }
                        })
                      }
                    }
                  }, '详细信息'),
                  h('Button', {
                    props: {
                      size: 'small',
                      type: 'text'
                    },
                    on: {
                      click: () => {
                        this.$Modal.error({
                          title: '驳回理由',
                          content: params.row.rejected
                        })
                      }
                    }
                  }, '驳回理由')
                ])
              } else {
                return h('div', [
                  h('Button', {
                    props: {
                      size: 'small',
                      type: 'text'
                    },
                    on: {
                      click: () => {
                        this.$router.push({
                          name: 'orderlist',
                          query: {
                            workid: params.row.work_id,
                            id: params.row.id,
                            status: params.row.status,
                            type: params.row.type
                          }
                        })
                      }
                    }
                  }, '详细信息')
                ])
              }
            }
          }
        ],
        page_number: 1,
        computer_room: util.computer_room,
        table_data: []
      }
    },
    methods: {
      currentpage (vl = 1) {
        axios.get(`${util.url}/myorder/?user=${sessionStorage.getItem('user')}&page=${vl}`)
          .then(res => {
            this.table_data = res.data.data
            this.table_data.forEach((item) => { (item.backup === 1) ? item.backup = '是' : item.backup = '否' })
            this.page_number = parseInt(res.data.page)
          })
          .catch(error => {
            util.err_notice(error)
          })
      }
    },
    mounted () {
      this.currentpage()
    }
  }
</script>
